
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n'
import { FlowerBoxFinishing } from '../../appTypes';
import BlockTemplate from "./BlockTemplate.vue";
import { getFinishing } from "../../modules/repository";

export default defineComponent({
  name: 'FlowerBoxSpec',
  emits: ["selectedBlockChanged", "lineChanged", "finishChanged"],
  components: {
    BlockTemplate
  },
  data () {
      return {
        line: "",
        finish: "",
        finishings: [] as FlowerBoxFinishing[],
        type: "standard" as "standard" | "skyline",
        selectedBlock: {}as { label: string, value: string },
        standardBlocks: [
          {
            value: "50x50_50",
            label: "50x50 H50",
            sizeH: 50,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x50_50",
            label: "25x50 H50",
            sizeH: 50,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x25_50",
            label: "50x25 H50",
            sizeH: 25,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x25_50",
            label: "25x25 H50",
            sizeH: 25,
            sizeV: 25,
            height: 50
          }
        ],
        skylineBlocks: [
          {
            value: "50x50_50",
            label: "50x50 H50",
            sizeH: 50,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x50_50",
            label: "25x50 H50",
            sizeH: 50,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x25_50",
            label: "50x25 H50",
            sizeH: 25,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x25_50",
            label: "25x25 H50",
            sizeH: 25,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x50_75",
            label: "50x50 H75",
            sizeH: 50,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x50_75",
            label: "25x50 H75",
            sizeH: 50,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x25_75",
            label: "50x25 H75",
            sizeH: 25,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x25_75",
            label: "25x25 H75",
            sizeH: 25,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x50_100",
            label: "50x50 H100",
            sizeH: 50,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x50_100",
            label: "25x50 H100",
            sizeH: 50,
            sizeV: 25,
            height: 50
          },
          {
            value: "50x25_100",
            label: "50x25 H100",
            sizeH: 25,
            sizeV: 50,
            height: 50
          },
          {
            value: "25x25_100",
            label: "25x25 H100",
            sizeH: 25,
            sizeV: 25,
            height: 50
          }
        ]
      }
  },
  props: ["lines"],
  setup() {
    const { t } = useI18n({
      inheritLocale: true,
      useScope: 'local'
    });

    return { 
        t
    };
  },
  watch: {
    selectedBlock: {
      handler: function (newVal) {
        this.$emit("selectedBlockChanged", newVal.value);
      },
      deep: true
    },
    line: {
      handler: function (newVal) {
        this.finish = "";
        this.finishings = getFinishing(newVal, "50x50");
        this.finish = this.finishings[0]?.id || "";
        this.$emit("lineChanged", newVal);
        this.$emit("finishChanged", this.finish);
      },
      deep: true
    },
  }
})
